@keyframes menu-main {
    17%{
        width: 15%;
        margin-left: -5%;
        border: 1px solid #000;
    }
    34%{
        width: 30%;
        margin-left: -10%;
    }
    51%{
        width: 45%;
        margin-left: -15%;
    }
    68%{
        width: 60%;
        margin-left: -20%;
    }
    85%{
        width: 70%;
        margin-left: -25%;
    }
    100%{
        width: 80%;
        margin-left: -30%;
    }
}
@keyframes menu-main-small {
    17%{
        width: 15%;
        margin-left: -5%;
        border: 1px solid #000;
    }
    34%{
        width: 30%;
        margin-left: -10%;
    }
    51%{
        width: 45%;
        margin-left: -15%;
    }
    68%{
        width: 60%;
        margin-left: -20%;
    }
    85%{
        width: 80%;
        margin-left: -25%;
    }
    100%{
        width: 95%;
        margin-left: -31%;
    }
}

@keyframes pokeCard {
    10%{
        background: linear-gradient(36deg, red, transparent, transparent);
    }
    20%{
        background: linear-gradient(72deg, red, transparent, transparent);
    }
    30%{
        background: linear-gradient(108deg, red, transparent, transparent);
    }
    40%{
        background: linear-gradient(144deg, red, transparent, transparent);
    }
    50%{
        background: linear-gradient(180deg, red, transparent, transparent); 
    }
    60%{
        background: linear-gradient(216deg, red, transparent, transparent);
    }
    70%{
        background: linear-gradient(252deg, red, transparent, transparent);
    }
    80%{
        background: linear-gradient(288deg, red, transparent, transparent);
    }
    90%{
        background: linear-gradient(324deg, red, transparent, transparent);
    }
    100%{
        background: linear-gradient(360deg, red, transparent, transparent);
    }
}
@keyframes pokeType {
    10%{
        background: linear-gradient(36deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    20%{
        background: linear-gradient(72deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    30%{
        background: linear-gradient(108deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    40%{
        background: linear-gradient(144deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    50%{
        background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%); 
    }
    60%{
        background: linear-gradient(216deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    70%{
        background: linear-gradient(252deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    80%{
        background: linear-gradient(288deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    90%{
        background: linear-gradient(324deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
    100%{
        background: linear-gradient(360deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 51%, rgba(255,0,0,1) 100%);
    }
}

@keyframes title-rotate {
    20%{
        transform: rotateY(72deg);
    }
    40%{
        transform: rotateY(144deg);
    }
    60%{
        transform: rotateY(216deg);
    }
    80%{
        transform: rotateY(288deg);
    }
    100%{
        transform: rotateY(360deg);
    }
}